main {
  width: 100%;
  height: 100%;
  position: relative;

  & > div {
    position: absolute;
    opacity: 0;
    transform: scale(0);
    transform-origin: bottom;
    transition: $transition;
  }
}

main div.active {
  width: 100%;
  height: 100%;
  transform: scale(1);
  opacity: 1;
}
